.appointment {
  width: 50%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.appointimg {
  width: 50%;
  height: 50vh;
  display: flex;
  /* align-items: center; */
  justify-content: center;
  /* border: 1px solid pink; */
}

.imgholder {
  min-height: 80%;
  height: auto;
  width: 90%;
  background-image: url(/static/media/appointImg.e93ca4cf.jpg);
  background-size: cover;
  /* border: 1px solid red ; */
