.blood-donation {
  background-color: hsl(197, 62%, 42%);
  /* margin-right: 5%; */
  width: 20%;
  height: 20%;
  font-size: 100%;
  color: rgb(2, 4, 10);
  font-family: "Roboto", sans-serif;
  border-radius: 5px;
}
.blood-donation:hover {
  width: 18%;
  height: 18%;
  font-size: 90%;
  transition-duration: 0.3s;
  border-radius: 5px;
  cursor: pointer;
}
.organ-donation {
  background-color: hsl(197, 62%, 42%);
  width: 20%;
  height: 20%;
  font-size: 100%;
  color: rgb(2, 4, 10);
  font-family: "Roboto", sans-serif;
  position: relative;
  border-radius: 8px;
}
.organ-donation:hover {
  width: 18%;
  height: 18%;
  font-size: 90%;
  transition-duration: 0.3s;
  border-radius: 8px;
  cursor: pointer;
}

